import React, { Fragment, useState } from "react";
import Header from "./componentes/Header";
import Fromulario from "./componentes/Fromulario";
import Mensaje from "./componentes/Mensaje";
import Resultado from "./componentes/Resultado";
import Spinner from "./componentes/Spinner";
import "./App.css";

function App() {
  const [cantidad, setCantidad] = useState(0);
  const [plazo, setPlazo] = useState("");
  const [total, setTotal] = useState(0);
  const [load, setLoad] = useState(false);

  let componente;
  if (load) {
    componente = <Spinner />;
  } else if (total === 0) {
    componente = <Mensaje />;
  } else {
    componente = <Resultado cantidad={cantidad} plazo={plazo} total={total} />;
  }
  return (
    <Fragment>
      <Header titulo="Cotizador de Prestamos" />
      <div className="container">
        <Fromulario cantidad={cantidad} setCantidad={setCantidad} plazo={plazo} setPlazo={setPlazo} setTotal={setTotal} setLoad={setLoad} />
        <div className="mensajes">{componente}</div>
      </div>
    </Fragment>
  );
}

export default App;
